@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,400;0,500;0,700;0,900;1,400;1,700;1,900&display=swap");
h1, h2, h3 {
  font-family: "Montserrat", sans-serif;
  margin: 0 0 2.5rem 0;
  font-size: 700;
  color: #111111;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #091421;
  font-size: 2rem;
}

