html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 700;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #ffd802;
  color: #111111;
}

nav {
  display: flex;
  justify-content: space-around;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.body {
  background-color: linear-gradient(#ff0000, #aee1f9);
  display: grid;
  gap: 1rem;
  grid-template-rows: auto auto 1fr auto;
  grid-template-columns: auto 1fr auto;
}

.banner {
  grid-area: 1/1;
  column-gap: 1rem;
}
.banner img {
  align-content: flex-end;
  max-width: auto;
}

.barra {
  grid-area: 2/1;
  background-color: #111111;
}
.barra ul {
  font-family: "Montserrat", sans-serif;
  font-size: 500;
}

.contenido {
  grid-area: 3/1;
  display: flex;
  flex-direction: column;
}

.footer {
  grid-area: 4/1;
  margin: 0 auto;
}

.boton {
  background-color: #111111;
  padding: 1.5rem 4rem;
  color: #ffffff;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  margin: 5rem 0;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: black;
  cursor: pointer;
}